const WebFormList = ({ webFields, removeFromList }) => {
  const list = webFields.map(({ _id, fieldName, fieldInForm, fieldFormVisible, fieldOptions }) =>
    fieldInForm ?
         (
      <li key={_id}>
        {fieldName}
        {!fieldFormVisible && fieldOptions.some(e => e.preSelected)
            ? `-> hidden in form with ${fieldOptions.filter(e => e.preSelected)} as preselected` :
            `-> visible in form`}
        <button id={fieldName} onClick={removeFromList}>
          X
        </button>
      </li>
    ) : null
  );

  return <ul>{list}</ul>;
};

export default WebFormList;
